9-
 -->
<template>
  <div class ="orderitem" v-if="isshow">
     <div class="topbox">
     <div class="shopbox">
        <img class="shopicon" :src="orderitem.shopicon">
        <p class="shopname">{{orderitem.shopname}}</p>
        <img class="nexticon" src="../../assets/next.png"> 
        </div>
        <p class="status">{{orderstate}}</p>
     </div>
     <div class ="contentbox" @click="seeDetails()">
        <div class="goodsbox">
            <img class="goodsimg" :src="orderitem.goodsimg">
            <div class="goodsinfobox">
                <p class="goodsname">{{orderitem.goodsname}}</p>
                <p class="goodsinfo">{{orderitem.goodsinfo}}</p>
            </div>
        </div>
        <div class="goodsnumbox">
            <p>{{orderitem.price}}</p>
            <p>x {{orderitem.number}}</p>
        </div>
     </div>
     <div class ="itembottombox">
        <p class="sumbox">{{orderitem.number}} {{totalprice}}</p>
        <div>
            <div class="operatebox" v-if="orderitem.status==1">
                <p class="button" @click="paynow()"></p>
                <p class="button" @click="cancleOrder"></p>
            </div>
            <div class="operatebox" v-if="orderitem.status==2">
                <p class="button" @click="tosend()"></p>
                <p class="button" @click="cancleOrder"></p>
            </div>
            <div class="operatebox" v-if="orderitem.status==3">
                <p class="button" @click="surearrival()"></p>
                <p class="button" @click="cancleOrder"></p>
            </div>
            <div class="operatebox" v-if="orderitem.status==4">
                <p class="button" @click="deleteOrder()"></p>
                <p class="button" @click="evaluate()"></p>
            </div>
            <div class="operatebox" v-if="orderitem.status==5">
                <p class="button" @click="deleteOrder()"></p>
            </div>
            <div class="operatebox" v-if="orderitem.status==9">
                <p class="button" @click="deleteOrder()"></p>
            </div>
        </div>
     </div>
     <div class="weui-mask" v-if="iscancle"></div>
    <div class="weui-dialog weui-skin_android" v-if="iscancle">
        <div class="weui-dialog__bd">                
                ?              
        </div>
        <div class="weui-dialog__ft">
            <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="cancleto()"></a>
            <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="iscancle = false;"></a>
        </div>
    </div>
     <div class="weui-mask" v-if="isdelete"></div>
    <div class="weui-dialog weui-skin_android" v-if="isdelete">
        <div class="weui-dialog__bd">                
                ?              
        </div>
        <div class="weui-dialog__ft">
            <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="deleteto()"></a>
            <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="isdelete = false;"></a>
        </div>
    </div>
     <div class="weui-mask" v-if="isarrival"></div>
    <div class="weui-dialog weui-skin_android" v-if="isarrival">
        <div class="weui-dialog__bd">                
                ?              
        </div>
        <div class="weui-dialog__ft">
            <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="arrivalto()"></a>
            <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="isarrival = false;"></a>
        </div>
    </div>
     <div class="weui-mask" v-if="ispay"></div>
    <div class="weui-dialog weui-skin_android" v-if="ispay">
        <div class="weui-dialog__bd">                
                ?              
        </div>
        <div class="weui-dialog__ft">
            <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="payto()"></a>
            <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="ispay = false;"></a>
        </div>
    </div>
  </div>
</template>
<script>
    import {
        WEB_SERVER as port
    } from '../../config';
    export default ({
        props: ['orderitem'],
        data: function() {
            return {
                iscancle: false,
                isdelete: false,
                isshow: true,
                isarrival: false,
                ispay: false,
                wronmessage: '',
                totalprice: '',
                orderstate: '',
            }
        },
        methods: {
            paynow() {
                this.ispay = true;
            },
            surearrival() {
                this.isarrival = true;
            },
            cancleOrder: function() {
                this.iscancle = true;
                console.log("");
            },
            deleteOrder: function() {
                this.isdelete = true;
                console.log("");
            },
            tosend() {
                alert('');
            },
            cancleto: function() {
                this.iscancle = false;
                var _self = this;
                $.ajax({
                    headers: {
                        'X-Token': $.cookie("token"),
                    },
                    timeout: 1000,
                    type: 'GET',
                    url: port + 'order/' + _self.orderitem.orderid + '/deleteOrderById/9',
                    success: function(data) {
                        console.log(data);
                        if (data.code == 200) {
                            _self.orderstate = "";
                            alert("");
                        } else {
                            console.log(data.message);
                        }
                    },
                    error: function() {
                        console.log("error");
                    }
                });
            },
            arrivalto() {
                this.isarrival = false;
                var _self = this;
                $.ajax({
                    headers: {
                        'X-Token': $.cookie("token"),
                    },
                    timeout: 1000,
                    type: 'GET',
                    url: port + 'order/' + _self.orderitem.orderid + '/deleteOrderById/4',
                    success: function(data) {
                        console.log(data);
                        if (data.code == 200) {
                            _self.orderstate = "";
                            alert("");
                        } else {
                            console.log(data.message);
                        }
                    },
                    error: function() {
                        console.log("error");
                    }
                });
            },
            payto() {
                this.ispay = false;
                var _self = this;
                $.ajax({
                    headers: {
                        'X-Token': $.cookie("token"),
                    },
                    timeout: 1000,
                    type: 'GET',
                    url: port + 'order/' + _self.orderitem.orderid + '/deleteOrderById/2',
                    success: function(data) {
                        console.log(data);
                        if (data.code == 200) {
                            _self.orderstate = "";
                            alert("");
                        } else {
                            console.log(data.message);
                        }
                    },
                    error: function() {
                        console.log("error");
                    }
                });
            },
            deleteto: function() {
                this.isdelete = false;
                var _self = this;
                $.ajax({
                    headers: {
                        'X-Token': $.cookie("token"),
                    },
                    timeout: 1000,
                    type: 'GET',
                    url: port + 'order/' + _self.orderitem.orderid + '/deleteById',
                    success: function(data) {
                        console.log(data);
                        if (data.code == 200) {
                            _self.isshow = false;
                            alert("");
                        } else {
                            console.log(data.message);
                        }
                    },
                    error: function() {
                        console.log("error");
                    }
                });
            },
            seeDetails: function() {
                var thiz = this;
                console.log("OrderItem-orderid:" + thiz.orderitem.orderid);
                var thiz = this;
                this.$store.dispatch("saveOrderId", thiz.orderitem.orderid).then(() => {
                    console.log("");
                }).catch(err => {
                    Toast('');
                });
                this.$router.push('OrderInfo');
            },
            evaluate: function() {
                var thiz = this;
                console.log("OrderItem-orderid:" + thiz.orderitem.orderid);
                var thiz = this;
                this.$store.dispatch("saveOrderId", thiz.orderitem.orderid).then(() => {
                    console.log("");
                }).catch(err => {
                    Toast('');
                });
                this.$router.push('Evaluate');
            }
        },
        created() {
            this.totalprice = parseFloat((this.orderitem.price * this.orderitem.number)).toFixed(2);
            //set order state
            switch (this.orderitem.status) {
                case 1:
                    this.orderstate = "";
                    break;
                case 2:
                    this.orderstate = "";
                    break;
                case 3:
                    this.orderstate = "";
                    break;
                case 4:
                    this.orderstate = "";
                    break;
                case 5:
                    this.orderstate = "";
                    break;
                case 9:
                    this.orderstate = "";
            }
        },
        mounted: function() {},
    })
</script>
<style scoped>
    .orderitem {
        width: 100%;
        max-width: 640px;
        height: 212px;
        user-select: none;
        -webkit-user-select: none;
        overflow: hidden;
        position: relative;
    }
    
    .topbox {
        border-top: solid 1px #e5e5e5;
        height: 40px;
        width: 100%;
        max-width: 640px;
        display: flex;
        -webkit-box-align: center;
        /* android 2.1-3.0, ios 3.2-4.3 */
        -webkit-align-items: center;
        /* Chrome 21+ */
        -ms-flex-align: center;
        /* WP IE 10 */
        align-items: center;
        /* android 4.4 */
        justify-content: space-between;
        /* android 4.4 */
    }
    
    .shopbox {
        display: flex;
        -webkit-box-align: center;
        /* android 2.1-3.0, ios 3.2-4.3 */
        -webkit-align-items: center;
        /* Chrome 21+ */
        -ms-flex-align: center;
        /* WP IE 10 */
        align-items: center;
        /* android 4.4 */
    }
    
    .shopicon {
        margin-left: 15px;
        width: 30px;
        height: 30px;
    }
    
    .shopname {
        display: inline-block;
        font-size: 14px;
        margin-left: 10px;
        margin-right: 10px;
    }
    
    .nexticon {
        width: 8px;
        height: auto;
    }
    
    .status {
        font-size: 14px;
        color: #2ad2c9;
        float: right;
        margin-right: 15px;
    }
    
    .contentbox {
        width: 100%;
        max-width: 640px;
        height: 100px;
        background: #fafafa;
        display: flex;
        justify-content: space-between;
        /* android 4.4 */
    }
    
    .goodsbox {
        background: #fafafa;
        text-align: left;
        padding-top: 10px;
        display: flex;
    }
    
    .goodsimg {
        width: 80px;
        height: auto;
        min-height: 60px;
        max-height: 80px;
        margin-left: 15px;
    }
    
    .goodsinfobox {
        margin-left: 5px;
        width: 60%;
    }
    
    .goodsinfo {
        color: gray;
        font-size: 12px;
    }
    
    .goodsname {
        font-size: 16px;
    }
    
    .itembottombox {
        width: 100%;
        max-width: 640px;
        height: 50px;
    }
    
    .goodsnumbox {
        font-weight: bold;
        font-size: 12px;
        padding-top: 40px;
        text-align: right;
        margin-top: 5px;
        margin-right: 15px;
    }
    
    .sumbox {
        display: block;
        width: 100%;
        max-width: 640px;
        text-align: right;
        padding: 5px;
        padding-right: 15px;
        border-bottom: solid 1px #e5e5e5;
    }
    
    .operatebox {
        height: 40px;
        width: 100%;
        max-width: 640px;
        display: flex;
        -webkit-box-align: center;
        /* android 2.1-3.0, ios 3.2-4.3 */
        -webkit-align-items: center;
        /* Chrome 21+ */
        -ms-flex-align: center;
        /* WP IE 10 */
        align-items: center;
        /* android 4.4 */
        justify-content: flex-end;
        /* android 4.4 */
    }
    
    .button {
        text-align: center;
        line-height: 30px;
        width: 80px;
        height: 30px;
        border-radius: 15px;
        border: solid 1px #e5e5e5;
        margin-right: 15px;
    }
</style>


